html {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  font-family: "Yu Gothic", "游ゴシック", YuGothic, "游ゴシック体", sans-serif;
  font-weight: 500;
  line-height: 2.0;
  letter-spacing: 0.1em; }

*, *::before, *::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

h2, h3, h4, h5 {
  margin: 0;
  padding: 0; }

img {
  width: 100%;
  vertical-align: bottom; }

figure {
  margin: 0; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

ul, li, dl, dt, dd {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  text-decoration: none;
  transition: all .6s ease; }

a:hover {
  transition: all .6s ease; }

.content-wrapper {
  width: 95%;
  margin: 0 auto;
  max-width: 1400px; }

section h2 {
  font-size: 2rem; }

/* Header */
.l-header {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;
  position: absolute;
  z-index: 1; }
  .l-header h1 {
    margin: 0 0 0 25px;
    font-size: 2.5rem;
    color: #fff; }
  .l-header nav ul {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    font-size: 1.2rem; }
  .l-header nav ul li {
    margin: 0 20px; }

header nav li a {
  color: #fff; }
  header nav li a:hover {
    opacity: 0.6; }

@media screen and (max-width: 1199px) {
  .l-hamburger {
    display: block;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height: 26px;
    cursor: pointer; }
  header nav {
    display: none; }
  .l-nav-button {
    position: relative;
    width: 30px;
    height: 26px;
    display: inline-block; }
    .l-nav-button span {
      display: inline-block;
      position: absolute;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: #fff; }
      .l-nav-button span:nth-child(1) {
        top: 0; }
      .l-nav-button span:nth-child(2) {
        top: 11px; }
      .l-nav-button span:nth-child(3) {
        bottom: 0; } }

/* MainVisual */
.c-mainvisual {
  position: relative; }
  .c-mainvisual img {
    height: 700px;
    object-fit: cover;
    object-position: 0 15%;
    -webkit-clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%);
    clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%); }
  .c-mainvisual h2 {
    position: absolute;
    top: 50%;
    left: 10%;
    transform: translateY(-50%);
    z-index: 1;
    font-size: 5vw;
    color: #fff; }

@media screen and (max-width: 1199px) {
  .c-mainvisual img {
    height: auto; } }
